export default function() {
  const button = document.getElementById('responsiveMenuButtonToggle');
  const mainResponsiveNavbar = document.getElementById('mainResponsiveNavbar');
  const mainResponsiveNavbarTitle = document.getElementById('mainResponsiveNavbarTitle');
  const links = mainResponsiveNavbar.getElementsByTagName('a');

  const classNameButtonActive = 'navbar__button_active';
  const classNameNavbarOpen = 'navbar_open';

  const onClickButtonToggle = () => {
    button.classList.toggle(classNameButtonActive);
    mainResponsiveNavbar.classList.toggle(classNameNavbarOpen);
  };

  button.addEventListener('click', onClickButtonToggle);
  for (let i = 0; i < links.length; i++) {
    links[i].addEventListener('click', onClickButtonToggle)
  }
  mainResponsiveNavbarTitle.addEventListener('click', onClickButtonToggle);
}